/* eslint-disable @typescript-eslint/no-explicit-any */
import { Directive, Input } from '@angular/core';
import {
  ControlValueAccessor, Validator
} from '@angular/forms';
import { FormFieldBase } from './form-base';

// Define a type for the options for better type safety
export interface IselectOption {
  value;
  label: string;
  disabled?: boolean; // Optional: if you want to disable specific options
}

@Directive({})
export class SelectFieldBase
  extends FormFieldBase
  implements ControlValueAccessor, Validator {
  // Input for the options array
  @Input() options: IselectOption[] = [];
  // Optional: Add an input for a default placeholder option
  @Input() placeholderOption?: string;


  constructor() {
    super();
  }

  onChangeEvent(event: Event): void {
    const target = event.target as HTMLSelectElement;
    // Find the corresponding option object if needed, or just use the value
    const selectedValue = target.value;

    this.updateValue(selectedValue);
  }


  onBlur(): void {
    this.handleBlur(); // Use the helper from base class
  }
}
